@import "../common/styles/sass/variables";
@import "../common/styles/sass/mixins";

.footerBlock {
    @include flex(row, normal, center, nowrap);
    .footerContainer {
        @include container();
        @include flex(column, normal, center, nowrap);
        min-height: 200px;
        .copyright {
            margin-bottom: 20px;
            @include text($textFontFamily, 20px, 20px, 400)
        }
    }
}








